a, span, *::before, *::after
	display: inline-block

html
	height: 100%


body
	font-family: 'FuturaPT', Arial, sans-serif
	min-height: 100%
	font-size: 16px
	display: flex
	flex-direction: column
	color: #000
	line-height: 1.3
	&::before
		content: none
		@media #{$mobileLandscape}
			content: attr(data-lanscape)
			position: fixed
			z-index: 9999
			top: 0
			left: 0
			right: 0
			bottom: 0
			font-size: 1.25rem
			display: flex
			background-color: #fff
			justify-content: center
			align-items: center
			text-align: center
			padding: 1.5rem

.container
	max-width: 1286px
	margin-left: auto
	margin-right: auto

.header
	background-color: #fff
	.verified &
		@media #{$max-xl}
			display: none

.main
	flex: 1 0 auto
	background-color: #F8F8FA


.logo
	&-link
		display: block
		font-size: 0
	height: auto
	max-width: 120px
	@media #{$xl}
		max-width: 180px

.main
	position: relative
	overflow-x: hidden

section.step
	&-intro
		padding: 1rem
		.active-2.verified &
			@media #{$max-xl}
				display: none
	&-verified
		.video-wrap
			display: none
			.active-2.verified &
				display: block
		.active-2 &
			@media #{$xl}
				margin: 0 auto 0
				min-height: 355px
				max-width: 630px
				background: url(../img/call_bg.jpg) center no-repeat
				background-size: cover
		.active-2.verified &
			@media #{$xl}
				margin-top: 55px


[class^=btn__label-step]
	display: none
.btn
	text-transform: uppercase
	text-align: center
	background-color: #DCE123
	width: 100%
	font-size: .875rem
	padding: 1.1rem
	font-weight: 700
	transition: .3s
	&:disabled
		cursor: wait
	&:hover:not(:disabled), &:active:not(:disabled)
		background-color: darken(#DCE123, 5%)
	&__label-step1
		.active-1 &
			display: inline
	&__label-step2
		.active-2:not(.new-attempt) &
			display: inline
	&__label-new-attempt
		display: none
		.new-attempt &
			display: inline

.nav
	position: fixed
	left: calc((100vw - 290px) / 2)
	right: calc((100vw - 290px) / 2)
	bottom: 12vh
	padding: 0 1.5rem
	display: none
	@media (min-width: 1440px) and (min-height: 650px)
		bottom: 45vh
	.active-1 &,
	.active-2 &
		display: block
	.active-2 &
		@media #{$xl}
			position: static
			display: flex
			justify-content: flex-end
			max-width: 630px
			margin: 0 auto
			padding: 0
			margin-top: .5rem
	.verified:not(.new-attempt) &
		display: none


.device-status
	padding: .5rem 0 .5rem 1.5rem
	position: relative
	transition: .3s
	&::before
		content: ""
		width: 16px
		height: 16px
		position: absolute
		top: 50%
		left: .15rem
		background: url(../img/checkmark.svg) no-repeat center
		transform: translateY(-50%)
	&.error
		&::before
			background: url(../img/close.svg) no-repeat center
	&__message
		color: #656565
		font-weight: 300
		.error &
			display: none
		&.error
			display: none
			.error &
				display: inline
	.new-attempt &
		display: none

.h1
	font-size: 2rem
	font-weight: 600
	text-align: center
	margin: 0 auto 1.25rem
	.mob
		@media #{$xl}
			display: none
	.desc
		@media #{$max-xl}
			display: none
	@media #{$xs}
		max-width: 180px

.pagination
	display: flex
	justify-content: space-between
	padding-left: 18px
	padding-right: 18px
	position: relative
	margin-bottom: 1.875rem
	max-width: 288px
	margin-left: auto
	margin-right: auto
	transition: .3s
	&::before,
	&::after
		content: ""
		width: 2rem
		height: 1px
		background-color: #D1D1D1
		position: absolute
		top: 50%
		transition: .3s
	&::before
		left: 25%
	&::after
		left: auto
		right: 25%
	&__item
		width: 2.5rem
		height: 2.5rem
		border-radius: 50%
		border: 1px solid transparent
		display: flex
		justify-content: center
		align-items: center
		background-color: #fff
		color: #656565
		font-size: 1.25rem
		transition: .3s
		&:nth-child(1)
			.active-1 &
				border-color: #00503C
				color: #00503C
				background-color: #EDFCF4
			.active-2 &,
			.active-3 &
				font-size: 0
				position: relative
				background-color: #EDFCF4
				&::before
					content: ""
					width: 10px
					height: 8px
					background: url(../img/checkmark.svg) center no-repeat
			.error.active-1 &
				border-color: #FA323C
				font-size: 0
				background: url(../img/close.svg) center no-repeat
		&:nth-child(2)
			.active-2 &
				border-color: #00503C
				color: #00503C
				background-color: #EDFCF4
			.active-3 &
				font-size: 0
				position: relative
				background-color: #EDFCF4
				&::before
					content: ""
					width: 10px
					height: 8px
					background: url(../img/checkmark.svg) center no-repeat
			.error.active-2 &
				border-color: #FA323C
				font-size: 0
				background: url(../img/close.svg) center no-repeat
		&:nth-child(3)
			.active-3 &
				border-color: #00503C
				color: #00503C
				background-color: #EDFCF4
			.error.active-3 &
				border-color: #FA323C
				font-size: 0
				background: url(../img/close.svg) center no-repeat
			.success.active-3 &
				font-size: 0
				&::before
					content: ""
					width: 10px
					height: 8px
					background: url(../img/checkmark.svg) center no-repeat

[class^=intro-text__step]
	display: none
.intro-text
	text-align: center
	font-size: 1.125rem
	max-width: 600px
	margin: 0 auto
	@media #{$max-xl}
		max-width: 380px
	&__step1
		.active-1 &
			display: inline

	&__step2
		.active-2:not(.verified) &
			display: inline

	&__step3
		@media #{$max-md}
			color: #656565
			font-family: Roboto, Arial, sans-serif
			font-size: .875rem
		&.sucess
			.active-3:not(.error) &
				display: inline
		&.error
			.active-3.error &
				display: inline

#debug
	position: fixed
	top: 0
	left: 0
	right: 0
	bottom: 0
	z-index: 9999
	padding: .5rem
	overflow: scroll
	background-color: #fff
	color: #000
	text-align: left