.video
	display: block
	width: 100%
	height: 100%
	transform: rotateY(180deg)
	&-wrap
		position: relative
		width: calc(100vh * 2)
		height: 100vh
		@media #{$xl}
			height: auto
			max-width: 100%

.canvas
	display: none

.instruction
	position: fixed
	bottom: 10vh
	left: 1rem
	right: 1rem
	background-color: #DCE123
	padding: 1.6rem 1rem
	z-index: 2
	text-align: center
	font-size: 1rem
	font-weight: 700
	transition: background-color .3s
	span
		white-space: nowrap
		text-overflow: ellipsis
		overflow: hidden
	&__icon-path
		stroke: #fff
		stroke-dashoffset: 745.74853515625
		stroke-dasharray: 745.74853515625
		animation: dash 2s ease-out forwards
	&__icon-success
		display: none
		width: 32px
		height: 32px
		.instruction.success &
			@media #{$max-xl}
				display: block
				position: absolute
				top: 50%
				left: 50%
				transform: translate(-50%, -50%)
				z-index: 1
	.new-attempt &
		@media #{$max-xl}
			display: none
	@media #{$xl}
		position: absolute
		left: 0
		right: 0
		bottom: 100%
		background-color: transparent
	&.success
		color: #1BBB5E
		&::after
			content: ""
			width: 32px
			height: 32px
			position: relative
			left: .5rem
			top: 4px
			background: url("data:image/svg+xml,%3Csvg viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.7585 4.19891C13.0617 4.48133 13.0822 4.96003 12.8043 5.26812L6.95321 11.7546C6.80802 11.9156 6.60149 12.0049 6.38669 11.9998C6.17189 11.9946 5.96974 11.8954 5.83219 11.7277L3.17262 8.48447C2.90933 8.16339 2.95202 7.68621 3.26797 7.41865C3.58392 7.15108 4.05348 7.19446 4.31678 7.51554L6.43097 10.0937L11.7064 4.2454C11.9843 3.93731 12.4553 3.9165 12.7585 4.19891Z' fill='%231BBB5E' /%3E%3C/svg%3E") center no-repeat
		@media #{$max-xl}
			background-color: #1BBB5E
			color: transparent
			&::after
				content: none


.frame
	position: fixed
	width: 260px
	height: 280px
	top: 50%
	left: 50%
	transform: translate(-50%, -75%)
	z-index: 1
	@media #{$xl}
		position: absolute
		transform: translate(-50%, -50%)
	&__item
		position: absolute
		border: 3px solid #fff
		width: 85px
		height: 95px
		&.tl
			top: 0
			left: 0
			border-bottom-width: 0
			border-right-width: 0
		&.tr
			top: 0
			left: auto
			right: 0
			border-bottom-width: 0
			border-left-width: 0
		&.br
			top: auto
			left: auto
			bottom: 0
			right: 0
			border-top-width: 0
			border-left-width: 0
		&.bl
			top: auto
			left: 0
			bottom: 0
			border-top-width: 0
			border-right-width: 0

@keyframes dash
	0%
		stroke-dashoffset: 745.74853515625

	100%
		stroke-dashoffset: 0