html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

blockquote, q {
  quotes: none;
}

blockquote:before, q:before, blockquote:after, q:after {
  content: "";
  content: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, q:before, blockquote:after, q:after {
  content: "";
  content: none;
}

button {
  background-color: transparent;
  padding: 0;
  outline: none;
  border: 0;
  cursor: pointer;
  color: inherit;
}

:focus {
  outline: none;
}

@font-face {
  font-family: "FuturaPT";
  src: url("../fonts/FuturaPTBook.woff2") format("woff2"), url("../fonts/FuturaPTBook.woff") format("woff");
  font-weight: 300;
  font-display: swap;
}
@font-face {
  font-family: "FuturaPT";
  src: url("../fonts/FuturaPTMedium.woff2") format("woff2"), url("../fonts/FuturaPTMedium.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "FuturaPT";
  src: url("../fonts/FuturaPTBold.woff2") format("woff2"), url("../fonts/FuturaPTBold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  unicode-range: U+0460-052F, U+1C80-1C88, U+20B4, U+2DE0-2DFF, U+A640-A69F, U+FE2E-FE2F;
}
.d-none {
  display: none !important;
}

a, span, *::before, *::after {
  display: inline-block;
}

html {
  height: 100%;
}

body {
  font-family: "FuturaPT", Arial, sans-serif;
  min-height: 100%;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  color: #000;
  line-height: 1.3;
}
body::before {
  content: none;
}
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
  body::before {
    content: attr(data-lanscape);
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    font-size: 1.25rem;
    display: flex;
    background-color: #fff;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 1.5rem;
  }
}

.container {
  max-width: 1286px;
  margin-left: auto;
  margin-right: auto;
}

.header {
  background-color: #fff;
}
@media (max-width: 1199px) {
  .verified .header {
    display: none;
  }
}

.main {
  flex: 1 0 auto;
  background-color: #F8F8FA;
}

.logo {
  height: auto;
  max-width: 120px;
}
.logo-link {
  display: block;
  font-size: 0;
}
@media (min-width: 1200px) {
  .logo {
    max-width: 180px;
  }
}

.main {
  position: relative;
  overflow-x: hidden;
}

section.step-intro {
  padding: 1rem;
}
@media (max-width: 1199px) {
  .active-2.verified section.step-intro {
    display: none;
  }
}
section.step-verified .video-wrap {
  display: none;
}
.active-2.verified section.step-verified .video-wrap {
  display: block;
}
@media (min-width: 1200px) {
  .active-2 section.step-verified {
    margin: 0 auto 0;
    min-height: 355px;
    max-width: 630px;
    background: url(../img/call_bg.jpg) center no-repeat;
    background-size: cover;
  }
}
@media (min-width: 1200px) {
  .active-2.verified section.step-verified {
    margin-top: 55px;
  }
}

[class^=btn__label-step] {
  display: none;
}

.btn {
  text-transform: uppercase;
  text-align: center;
  background-color: #DCE123;
  width: 100%;
  font-size: 0.875rem;
  padding: 1.1rem;
  font-weight: 700;
  transition: 0.3s;
}
.btn:disabled {
  cursor: wait;
}
.btn:hover:not(:disabled), .btn:active:not(:disabled) {
  background-color: #cace1c;
}
.active-1 .btn__label-step1 {
  display: inline;
}
.active-2:not(.new-attempt) .btn__label-step2 {
  display: inline;
}
.btn__label-new-attempt {
  display: none;
}
.new-attempt .btn__label-new-attempt {
  display: inline;
}

.nav {
  position: fixed;
  left: calc((100vw - 290px) / 2);
  right: calc((100vw - 290px) / 2);
  bottom: 12vh;
  padding: 0 1.5rem;
  display: none;
}
@media (min-width: 1440px) and (min-height: 650px) {
  .nav {
    bottom: 45vh;
  }
}
.active-1 .nav, .active-2 .nav {
  display: block;
}
@media (min-width: 1200px) {
  .active-2 .nav {
    position: static;
    display: flex;
    justify-content: flex-end;
    max-width: 630px;
    margin: 0 auto;
    padding: 0;
    margin-top: 0.5rem;
  }
}
.verified:not(.new-attempt) .nav {
  display: none;
}

.device-status {
  padding: 0.5rem 0 0.5rem 1.5rem;
  position: relative;
  transition: 0.3s;
}
.device-status::before {
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 0.15rem;
  background: url(../img/checkmark.svg) no-repeat center;
  transform: translateY(-50%);
}
.device-status.error::before {
  background: url(../img/close.svg) no-repeat center;
}
.device-status__message {
  color: #656565;
  font-weight: 300;
}
.error .device-status__message {
  display: none;
}
.device-status__message.error {
  display: none;
}
.error .device-status__message.error {
  display: inline;
}
.new-attempt .device-status {
  display: none;
}

.h1 {
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
  margin: 0 auto 1.25rem;
}
@media (min-width: 1200px) {
  .h1 .mob {
    display: none;
  }
}
@media (max-width: 1199px) {
  .h1 .desc {
    display: none;
  }
}
@media (max-width: 575px) {
  .h1 {
    max-width: 180px;
  }
}

.pagination {
  display: flex;
  justify-content: space-between;
  padding-left: 18px;
  padding-right: 18px;
  position: relative;
  margin-bottom: 1.875rem;
  max-width: 288px;
  margin-left: auto;
  margin-right: auto;
  transition: 0.3s;
}
.pagination::before, .pagination::after {
  content: "";
  width: 2rem;
  height: 1px;
  background-color: #D1D1D1;
  position: absolute;
  top: 50%;
  transition: 0.3s;
}
.pagination::before {
  left: 25%;
}
.pagination::after {
  left: auto;
  right: 25%;
}
.pagination__item {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  border: 1px solid transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  color: #656565;
  font-size: 1.25rem;
  transition: 0.3s;
}
.active-1 .pagination__item:nth-child(1) {
  border-color: #00503C;
  color: #00503C;
  background-color: #EDFCF4;
}
.active-2 .pagination__item:nth-child(1), .active-3 .pagination__item:nth-child(1) {
  font-size: 0;
  position: relative;
  background-color: #EDFCF4;
}
.active-2 .pagination__item:nth-child(1)::before, .active-3 .pagination__item:nth-child(1)::before {
  content: "";
  width: 10px;
  height: 8px;
  background: url(../img/checkmark.svg) center no-repeat;
}
.error.active-1 .pagination__item:nth-child(1) {
  border-color: #FA323C;
  font-size: 0;
  background: url(../img/close.svg) center no-repeat;
}
.active-2 .pagination__item:nth-child(2) {
  border-color: #00503C;
  color: #00503C;
  background-color: #EDFCF4;
}
.active-3 .pagination__item:nth-child(2) {
  font-size: 0;
  position: relative;
  background-color: #EDFCF4;
}
.active-3 .pagination__item:nth-child(2)::before {
  content: "";
  width: 10px;
  height: 8px;
  background: url(../img/checkmark.svg) center no-repeat;
}
.error.active-2 .pagination__item:nth-child(2) {
  border-color: #FA323C;
  font-size: 0;
  background: url(../img/close.svg) center no-repeat;
}
.active-3 .pagination__item:nth-child(3) {
  border-color: #00503C;
  color: #00503C;
  background-color: #EDFCF4;
}
.error.active-3 .pagination__item:nth-child(3) {
  border-color: #FA323C;
  font-size: 0;
  background: url(../img/close.svg) center no-repeat;
}
.success.active-3 .pagination__item:nth-child(3) {
  font-size: 0;
}
.success.active-3 .pagination__item:nth-child(3)::before {
  content: "";
  width: 10px;
  height: 8px;
  background: url(../img/checkmark.svg) center no-repeat;
}

[class^=intro-text__step] {
  display: none;
}

.intro-text {
  text-align: center;
  font-size: 1.125rem;
  max-width: 600px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .intro-text {
    max-width: 380px;
  }
}
.active-1 .intro-text__step1 {
  display: inline;
}
.active-2:not(.verified) .intro-text__step2 {
  display: inline;
}
@media (max-width: 767px) {
  .intro-text__step3 {
    color: #656565;
    font-family: Roboto, Arial, sans-serif;
    font-size: 0.875rem;
  }
}
.active-3:not(.error) .intro-text__step3.sucess {
  display: inline;
}
.active-3.error .intro-text__step3.error {
  display: inline;
}

#debug {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  padding: 0.5rem;
  overflow: scroll;
  background-color: #fff;
  color: #000;
  text-align: left;
}

.video {
  display: block;
  width: 100%;
  height: 100%;
  transform: rotateY(180deg);
}
.video-wrap {
  position: relative;
  width: 200vh;
  height: 100vh;
}
@media (min-width: 1200px) {
  .video-wrap {
    height: auto;
    max-width: 100%;
  }
}

.canvas {
  display: none;
}

.instruction {
  position: fixed;
  bottom: 10vh;
  left: 1rem;
  right: 1rem;
  background-color: #DCE123;
  padding: 1.6rem 1rem;
  z-index: 2;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
  transition: background-color 0.3s;
}
.instruction span {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.instruction__icon-path {
  stroke: #fff;
  stroke-dashoffset: 745.7485351563;
  stroke-dasharray: 745.7485351563;
  animation: dash 2s ease-out forwards;
}
.instruction__icon-success {
  display: none;
  width: 32px;
  height: 32px;
}
@media (max-width: 1199px) {
  .instruction.success .instruction__icon-success {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
  }
}
@media (max-width: 1199px) {
  .new-attempt .instruction {
    display: none;
  }
}
@media (min-width: 1200px) {
  .instruction {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 100%;
    background-color: transparent;
  }
}
.instruction.success {
  color: #1BBB5E;
}
.instruction.success::after {
  content: "";
  width: 32px;
  height: 32px;
  position: relative;
  left: 0.5rem;
  top: 4px;
  background: url("data:image/svg+xml,%3Csvg viewBox='0 0 16 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.7585 4.19891C13.0617 4.48133 13.0822 4.96003 12.8043 5.26812L6.95321 11.7546C6.80802 11.9156 6.60149 12.0049 6.38669 11.9998C6.17189 11.9946 5.96974 11.8954 5.83219 11.7277L3.17262 8.48447C2.90933 8.16339 2.95202 7.68621 3.26797 7.41865C3.58392 7.15108 4.05348 7.19446 4.31678 7.51554L6.43097 10.0937L11.7064 4.2454C11.9843 3.93731 12.4553 3.9165 12.7585 4.19891Z' fill='%231BBB5E' /%3E%3C/svg%3E") center no-repeat;
}
@media (max-width: 1199px) {
  .instruction.success {
    background-color: #1BBB5E;
    color: transparent;
  }
  .instruction.success::after {
    content: none;
  }
}

.frame {
  position: fixed;
  width: 260px;
  height: 280px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -75%);
  z-index: 1;
}
@media (min-width: 1200px) {
  .frame {
    position: absolute;
    transform: translate(-50%, -50%);
  }
}
.frame__item {
  position: absolute;
  border: 3px solid #fff;
  width: 85px;
  height: 95px;
}
.frame__item.tl {
  top: 0;
  left: 0;
  border-bottom-width: 0;
  border-right-width: 0;
}
.frame__item.tr {
  top: 0;
  left: auto;
  right: 0;
  border-bottom-width: 0;
  border-left-width: 0;
}
.frame__item.br {
  top: auto;
  left: auto;
  bottom: 0;
  right: 0;
  border-top-width: 0;
  border-left-width: 0;
}
.frame__item.bl {
  top: auto;
  left: 0;
  bottom: 0;
  border-top-width: 0;
  border-right-width: 0;
}

@keyframes dash {
  0% {
    stroke-dashoffset: 745.7485351563;
  }
  100% {
    stroke-dashoffset: 0;
  }
}